
* {
    box-sizing: border-box;
}

body {
    background-color: $color-background;
    color: $color-text;
}

.container {
    margin: 0 10%;
}

.box {
    background-color: $color-box-background;
    border-radius: 8px;
    border: 1px solid $color-box-border;
}

.hidden {
    display: none;
}